//Delete Account and Profile
export const deleteAccount = () => async (dispatch) => {
	if (
		window.confirm('Are you sure you want to delete your account permamently?')
	)
		try {
			await axios.delete(`/api/profile/`);
			dispatch({
				type: CLEAR_PROFILE,
			});
			dispatch({
				type: ACCOUNT_DELETED,
			});
			dispatch(setAlert('Account Deleted, Succcessfully '));
		} catch (err) {
			dispatch({
				type: PROFILE_ERROR,
				payload: { msg: err.response.statusText, status: err.response.status },
			});
		}
};
